.feature {
    display: flex;
    align-items: flex-start;
    margin-bottom: 2rem;

}

.faeture-icon {
    position: relative;
    margin-right: 2rem;

}

.inner-icon {
    position: absolute;
    /* top: 50%;
    left: 50%; */
    transform: translate(-80%, -80%);
}

.feature-text h3 {
    font-size: 2rem;
    font-weight: 400;
    line-height: 1.4;
    text-transform: uppercase;
    color: var(--light-blue);
}